:root {
  --background-color: #f1f5f9;
  --primary-color: #ffa499;
  --secondary-color: #ffe8e5;
  --background-white: #ffffff;
  --font-family: "Montserrat", sans-serif;
  --h2-size: 32px;
  --h3-size: 24px;
  --h4-size: 16px;
  --link-size: 20px;
  --regular-weight: 400;
  --medium-weight: 600;
  --heavy-weight: 700;
  --box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

body {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template: minmax(75px, auto) 1fr / minmax(200px, 1fr) 3fr;
  background: var(--background-color);
  font-family: var(--font-family);
}

.header-container {
  grid-area: 1 / 1 / 2 / 3;
  background-color: var(--primary-color);
  padding: 10px;
}

.header-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header-title {
  font-size: var(--h2-size);
  color: var(--background-color);
  font-weight: var(--heavy-weight);
}

.sidebar-navigation {
  background: var(--secondary-color);
  grid-area: 2 / 1 / 3 / 2;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.divider-line {
  margin: 0;
  border: 1px dashed var(--primary-color);
}

.project-form {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.project-form-name,
.project-form-submit,
.project-todo-add,
.project-item,
.project-delete-btn,
.list-delete-btn,
.project-todo,
.todo-list-text,
.todo-list-desc,
.todo-list-date,
.todo-list-option,
.add-list-item {
  padding: 8px 14px;
  background-color: var(--background-white);
  outline: 0;
  border: 0;
  border-radius: 5px;
  color: #000000;
  box-shadow: var(--box-shadow);
  font-family: var(--font-family);
}

.project-form-name {
  color: #24292e;
  outline: none;
  flex: 5 5 auto;
}

.project-form-submit {
  cursor: pointer;
  flex: 1 1 auto;
}

.project-list-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.project-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.project-item:hover {
  background-color: var(--background-color);
}

.project-item p {
  font-weight: bold;
}

.project-item button,
.project-delete-btn,
.project-todo-add,
.list-delete-btn {
  cursor: pointer;
}

.project-item button:hover,
.project-delete-btn:hover,
.project-todo-add:hover,
.list-delete-btn:hover {
  background-color: var(--primary-color);
}

.main-content {
  grid-area: 2 / 2 / 3 / 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 20px;
}

.main-content h2 {
  font-size: var(--h2-size);
  font-weight: var(--heavy-weight);
}

.project-todo {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr 1fr 1fr;
  width: 90%;
  align-items: center;
  gap: 20px;
}

.add-list-item {
  display: grid;
  grid-template-columns: 1fr 4fr 2fr 1fr 1fr;
  width: 90%;
  align-items: center;
  gap: 20px;
}

.list-item-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  width: 90%;
}

.list-item {
  width: 100%;
}

.column-headings {
  font-weight: var(--heavy-weight);
}

.active {
  background-color: #e5ffe8;
}

.project-warning {
  color: #ff0000;
  font-weight: bold;
  display: none;
}
